<template>
   
    <el-menu
      default-active="2"
      class="menu"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :collapse="isCollapse" 
    >   <!-- collapse菜单栏的缩放 -->
      <h3>期刊在线投稿系统</h3>

      <el-menu-item @click="clickMenu(item)" v-for="item in noChildren" :key="item.name" :index="item.label">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{item.label}}</span>
      </el-menu-item>

      <el-submenu  v-for="item in hasChildren" :key="item.label" :index="item.label">
        <template slot="title">
          <i :class="`el-icon-${item.icon}`"></i>
          <span slot="title">{{item.label}}</span>
        </template>
        <el-menu-item-group v-for="subItem in item.children" :key="subItem.name" :index="subItem.name">
          <el-menu-item index="1-1" @click="clickMenu(subItem)">{{subItem.label}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

    </el-menu>
</template>

<script>
export default {
  methods: {
    clickMenu(item)
    {
      //当前路径和目标路径不一样，才跳转;同时，如果当前路由位置是/home,目标位置是'/',重定向后是一致的，不跳转
      if(this.$route.path !== item.path && !(this.$route.path == '/home' && item.path === '/'))
        this.$router.push(item.path)
    }
  },
  data() {
    return {
      isCollapse: false,
      menuData: [
        {
          path: "/",
          name: "home",
          label: "首页",
          icon: "s-home",
          url: "Home/Home",
        },
        {
          path: "/mall",
          name: "mall",
          label: "商品管理",
          icon: "video-play",
          url: "MallManage/MallManage",
        },
        {
          path: "/user",
          name: "user",
          label: "用户管理",
          icon: "user",
          url: "UserManage/UserManage",
        },
        {
          label: "其他",
          icon: "location",
          children: [
            {
              path: "/page1",
              name: "page1",
              label: "页面1",
              icon: "setting",
              url: "Other/PageOne",
            },
            {
              path: "/page2",
              name: "page2",
              label: "页面2",
              icon: "setting",
              url: "Other/PageTwo",
            },
          ],
        },
      ],
    };
  },
  computed:{
    //没有子菜单的菜单数据
    noChildren(){
      console.log("1")
      return this.menuData.filter(item=> !item.children)
    },
    hasChildren(){
      return this.menuData.filter(item=> item.children)
    }

  }
};
</script>

<style scoped>
.menu {
  height:100vh;
}
h3{
  text-align: center;
  line-height:48px;
  color:#fff;
  font-weight:400;
  font-size:16px;
}
</style>

